import styles from './Slider.module.css'; 
const Slider = ({name, value, onChange}) => {
  return (
    <div className={styles['slider']}>
      <span className={styles['span']}>{name} <strong className={styles['strong']}>{value}%</strong></span>
      <input 
        type="range" 
        name={name} 
        min="0" 
        max="100"
        step="1"
        value={value}
        onChange={onChange}
      />
    </div>
  )
}

export default Slider
