import  rand  from "random-key";
import classes from './TableUi.module.css'; 


const TableUi = props => {
  const {headers, parsedData} = props.data; 

  const setActiveCell = (cellContent) => {
    console.log(typeof cellContent);
    if(typeof cellContent === 'number' && cellContent !== 0){
      return classes['active']; 
    }
  }

  const td = () => {
    const getTd = (e) => {
      const tdInfo = [];
      for (let key in e) {
        tdInfo.push(e[key]); 
      }; 
      return tdInfo;
    } 
    return parsedData.map(elem => {
      return (
        <tr className={classes['tr-table']} key={rand.generate()} >
          {getTd(elem).map(e => {
            
            return <td className={[classes['td'], setActiveCell(e)].join(' ')} key={rand.generate()}>{e}</td>
          })}
        </tr>
      )
    }) 
  }
  return (
    <table>
      <thead>
      <tr>
        {headers.map(e => <th key={rand.generate()} className={[classes['tfoot']].join(' ')}>{e}</th>)}
      </tr>
      </thead>
      <tbody>{td()}</tbody>
    </table>
  ); 
}
export default TableUi; 