import React, {useState} from 'react'
import Slider from '../../UI/Slider/Slider'; 
import NamesInput from '../Sprint2/NamesInput';
import {getKNeighbors} from '../Sprint2/CosenusSimilarity/getKNeighbors'; 
import Neighbor from '../../UI/Neighbor/Neighbor'; 
import TableUi from '../../UI/TableUi'; 
import {std} from 'mathjs'; 
import user from '../../img/user.svg'; 
import share from '../../img/share.svg'; 
import aggregationIcon from '../../img/aggregation.svg'; 


const aggregationSystem = [
  'Naive average',
  'Least misery',
  'Maximum pleasure',
  'Media satisfaction',
]

const Sprint4 = ({data, secondData}) => {
  const {parsedData: mainData, headers} = data; 
  const {parsedData: compareData, headers: secondHeaders} = secondData; 
  const [asideHeigh, setAsideHeigh] = useState(300)
  const nameHeader = headers[0]; 
  const secondHeader = secondHeaders[0]
  const names = mainData.map(name => name[nameHeader]);
  const [arrayResult, setArrayResult ] = useState([]) ; 
  const [pizzaNeighbor, setPizzaNeighbor] = useState([])
  const [person, setPerson] = useState(names[0]); 
  const [aggregation, setAggregation] = useState(aggregationSystem[0]); 
  const [neighbors, setNeighbors] = useState('0'); 
  const dataValues = Object.keys(mainData[0]).splice(1); 
  const defaultData = dataValues.map( dv => ({id: dv.split(' ').join(''), value: 100}))
  const [values, setValues] = useState(defaultData); 
  const [newVector, setNewVector] = useState(); 
  const [showTable, setShowTable] = useState(false); 
  

  const getStandardDeviationFromNeighbor = (currentData) => {
    const newV = currentData.map(v => {
      const { [nameHeader]: name, ...rest} = v;
      return rest; 
    })
  
    const objArray = newV.reduce((acc, val) => {
      for(const key in val){
        if(!acc[key]){
          acc[key] = []
        }
        acc[key].push(val[key])
      }
      return acc
    }, {}); 
    for(const key in objArray){
      objArray[key] = std(objArray[key])
    }
    return objArray; 
  }

  const getTransformedData = (currentData, rangeValues) => {
    return currentData.map( dValue => {
      const  weightSystem = {};
      let index = 0;
      for (const key in dValue){
        if(key !== nameHeader){
          weightSystem[key] = (parseInt(dValue[key])*rangeValues[index]).toString();
          index ++;
        }else{
          weightSystem[key] = dValue[key];
        }
      }
      return weightSystem
    })
  }

  const getTemporalVector = (transformedData ) => {
    const newV = transformedData.map(v => {
      const { [nameHeader]: name, ...rest} = v;
      return rest; 
    })
    const { length } = newV;
    const vectorTest = {}; 
    newV.forEach((element, index) => {
      if(index !== 0) return 
      for (const key in element){
        vectorTest[key] = newV.reduce((acc, val) => {
          return acc + (val[key]/length);
        }, 0);
      }
    });
    for(const key in vectorTest){
      vectorTest[key] = Math.round(vectorTest[key])
    }
    return vectorTest;
  }

  const getSystemAggregationTVector = (system, tVector, neighborsVector) => {
    const newVector = {}; 

    switch (system) {
      case 'Naive average':
        return tVector
      case 'Least misery':
        for(const key in tVector){
          newVector[key] = tVector[key] < 5 ? 0 : tVector[key]
        }
        return newVector
      case 'Maximum pleasure':
          for(const key in tVector){
            newVector[key] = tVector[key] >= 8 ? tVector[key]: 0; 
          }
          return newVector
      case 'Media satisfaction':
        const standardVector = getStandardDeviationFromNeighbor(neighborsVector); 
        console.table(standardVector);
        for(const key in standardVector){
          if(standardVector[key] > 1){
            newVector[key] = 0; 
          }else{
            newVector[key] = tVector[key]; 
          }
        }
        return newVector
      default: 
        return tVector; 
    }
  }

  const handleSubmit = (e) => {
    e.preventDefault();
    const rangeValues = transformRangeValue(values); 
    const transformedData = getTransformedData(mainData, rangeValues); 
    const currentPersonData = transformedData.find(name => name[nameHeader]===person);
    const arrayR = getKNeighbors(transformedData,currentPersonData,[nameHeader], neighbors); 
    setArrayResult(arrayR); 
    const neighborsVector = transformedData.filter(n => {
      return arrayR.map( a => a.name).includes(n[nameHeader]); 
    })
    const tempVec = getTemporalVector(neighborsVector);
    const protoPerson = {[nameHeader]: 'Proto person ðŸ¥¸', ...tempVec}
    
    const aggregationVectorData = getSystemAggregationTVector(aggregation, tempVec, neighborsVector);
    const aggregationPerson= {[nameHeader]: 'Aggregation person ðŸ˜¶', ...aggregationVectorData}; 

    
    setNewVector({headers: headers, parsedData: [protoPerson, aggregationPerson]}); 

    const pizzasNeighborData = getKNeighbors(compareData, aggregationPerson, [nameHeader, secondHeader], neighbors, true); 
    setPizzaNeighbor(pizzasNeighborData); 
    
    setShowTable(neighbors !== ('0' || ''))

    
  }

  //set
  const onChange = (id, newData) =>{
    const copy = [...values]; 
    const current = copy.findIndex(e => e.id === id); 
    if(current === -1) return
    copy[current].value = newData; 
    setValues(copy); 
  }

  const transformRangeValue = (valuesArray) => {
    return valuesArray.map(va => (va.value)/100)
  }

  const inputHandler = (e) => {
    if(/^\d+$/.test(e.target.value) || e.target.value === '') setNeighbors(e.target.value )
  }

  return (
    <div className="sprint sprint4">
      <h2>Sprint 4</h2>
      <form onSubmit={handleSubmit} className="sprint4__form">  
        <div className="sprint4__interaction">
          <aside 
            className="sprint4__actions"
            ref={ aside => {
              if(!aside) return 
              setAsideHeigh(aside.getBoundingClientRect().height);
            }} 
          >
            <label>
              <span>Person</span>
              <NamesInput names={names} onSetValue={setPerson}/>
            </label>
            <label>
              <span>Neighborhood Size</span>
              <input 
              className="input-number"
              value={neighbors} 
              onChange={inputHandler} 
              type="text" />
            </label>
            <label>
              <span>Aggregation System</span>
              <NamesInput names={aggregationSystem} onSetValue={setAggregation}/>
            </label>
          </aside>
          <aside 
            className="slider-container"
            style={{
              height: `${asideHeigh}px`
            }}
          >
            {values.map((elem, index) => (
              <Slider
                key={elem.id}
                name={elem.id}
                onChange={(e) => onChange(elem.id, parseFloat(e.target.value))}
                value={elem.value}
              />
            ))}
          </aside>
        </div>
        <div className="btn-container">
          <div className="sprint4__set-info-container">  
            <div className="sprint4__set-info">
              <img src={user} alt="" />
              <span>{person}</span>
            </div>
            <div className="sprint4__set-info">
              <img src={share} alt="" />
              <span>Size {neighbors}</span>
            </div>
            <div className="sprint4__set-info">
              <img src={aggregationIcon} alt="" />
              <span>{aggregation}</span>
            </div>
          </div>
          <button className="sprint4__submit">Submit</button>
        </div>
      </form>
      <h3>Neighborhood</h3>
      <ul>
        {arrayResult.map((elem, index) => (
          <Neighbor 
            key={index}
            index={index+1} 
            name={elem.name} 
            value={`${(elem.tie).toFixed(1)}%`}
          />
        ))}
      </ul>
      {showTable && <TableUi data={newVector}/>}
      <h3>Pizzas</h3>
      <ul>
        {pizzaNeighbor.map((elem, index) => (
          <Neighbor 
            key={index}
            index={index+1} 
            name={elem.name} 
            value={`${(elem.tie).toFixed(1)}%`}
          />
        ))}
      </ul>
    </div>
  )
}

export default Sprint4
